import React from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { shallowEqual } from 'react-redux';
import Status from '../../../../components/Status';
import MiniBox from '../../../../layout/MiniBox';
import globalStyle from '../../../../Global.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { updateSetupWalletOptions } from '../../../../store/actionCreators/account';

/**
 * Kyb Approved - Component for showing bussiness approved status.
 * @module KYB
 * @returns {JSX.Element}
 */
const KybApproved = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();

  const { selectedBusiness } = useAppSelector((store) => store.business, shallowEqual);

  return (
    <div className="page">
      <MiniBox varient="box" bodyClass={globalStyle.sucessScreenNoVPadding}>
        <Status
          status="success"
          title={t('business_approved.title')}
          description={t('business_approved.description')}
          ctaText={t('business_approved.ctaText')}
          showCTA
          onNext={() => {
            if (selectedBusiness) {
              dispatch(updateSetupWalletOptions({
                accountType: 'cash',
                businessId: selectedBusiness.id,
              }));
              navigate('/ao/account/setup');
            } else {
              navigate('/ao/account/setup-options');
            }
          }}
        />
      </MiniBox>
    </div>
  );
};

export default KybApproved;
